.l-one {
  width: 100vw;
  height: 100vh;
  /* border:1px solid red; */
  /* background-color:#252323; */
  background-color: white;
}
.google-auth {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3%;
}

.fb-auth {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3%;
}

.icons-div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-icons {
  font-size: 15px;
  color: blue;
  align-items: center;
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 50%;
  margin: 4px;
  padding: 9px;
}
.l-two {
  position: absolute;
  z-index: 1;
  clip-path: polygon(44% 0, 71% 49%, 44% 100%, 0% 100%, 24% 49%, 0% 0%);
  background: #7f7fd5;
  background: -webkit-linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  width: 100%;
  height: 100%;
  animation: l-two-anim 1s ease-in-out;
}

@keyframes l-two-anim {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}

.l-three {
  position: absolute;
  top: 25%;
  left: 36%;
  z-index: 2;
  background-color: white;
  border-radius: 12px;
  transition: ease-in-out 1s;
  width: 25%;
  animation: l-three-anim 1s ease-in-out;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.7);
}

.su-three {
  top: 18%;
  animation: su-three-anim 500ms ease-in-out;
}

@keyframes su-three-anim {
  from {
    top: 0%;
  }
  to {
    top: 18%;
  }
}

@keyframes l-three-anim {
  from {
    top: 0%;
  }
  to {
    top: 25%;
  }
}

.heading {
  text-align: center;
  /* border-bottom: 5px solid red; */
  border-bottom: 5px solid rgb(55, 186, 218);
  animation: heading1 1s;
}
@keyframes heading1 {
  from {
    font-size: 0;
  }
  to {
    font-size: 15px;
    border-width: 100%;
  }
}
.heading h1 {
  margin-top: 19px;
  display: inline-block;
}

.l-input-control {
  /* border: 2px solid green; */
  align-items: center;
  text-align: center;
}
.l-input-common {
  display: block;
  margin: auto;
  margin-bottom: 28px;
  /* margin-top: 23px; */
  width: 80%;
  border: 1px solid grey;
  padding: 14px;
  font-size: 13px;
  border-radius: 9px;
  font-weight: 500;
}

.l-formbtn {
  text-align: center;
}

.l-formbtn-1 {
  border: 1px solid grey;
  border-radius: 8px;
  font-size: 16px;
  padding: 0px 0px;
  margin: 0px 0px;
  margin-bottom: 19px;
  padding: 6px 25px 6px 25px;
  cursor: pointer;
  width: 89%;
  height: 12%;
  transition: ease-in-out 500ms;
  font-weight: 600;
}

.l-formbtn-1:hover {
  background-color: #cc5333;
  color: white;
}

@media (max-width: 820px) {
  .l-two {
    clip-path: polygon(56% 0, 100% 50%, 56% 99%, 0% 100%, 38% 49%, 0% 0%);
  }
  .l-three {
    left: 5%;
    width: 90%;
  }

  .l-formbtn-1 {
    width: 90%;
  }

  .su-three {
    top: 18%;
  }
}
