@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap');


.aboutus{
    background-color: #faf6f6;
    margin-bottom: 10px;
  /* height: 340vh; */
    
}
.abt-one {
    width: 100vw;
    height: 630px;
    background-color: #252323;
    margin: 0;
    padding: 0;
    z-index: 1;
    /* border: 3px solid red; */
}

.h-three-contact {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:  #faf6f6;;
}

.h-three-contact-child {
    margin: 10px;
    padding: 49px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

.h-three-contact-p1 {
    padding: 45px;
}
.h-three-inner{
    background-color:white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.abt-two {
    width: 100%;
    height: 100%;
    background: #7F7FD5;
    background: -webkit-linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);
    background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);
    clip-path: polygon(0 0, 0 85%, 100% 0);
    /* border-radius: 30% 70% 70% 30% / 30% 30% 70% 70% ; */
    text-align: right;
    margin: 0;
    padding: 0;
    position: absolute;
    animation: abt-two-anim 1s ease-in-out;
}
.abt-h2-2 {
    margin: 30px;
    font-size: 36px;
    padding: 16px;

}
@keyframes abt-two-anim {
    from {
        height: 0;
    }

    to {
        height: 100%;
    }
}

.abt-h1 {
    z-index: 100;
    position: absolute;
    top: 23%;
    left: 3%;
    color: white;
    font-size: 100px;
    animation: abt-h1-anim 1s ease-in-out;
}

@keyframes abt-h1-anim {
    from {
        font-size: 0px;
    }

    to {
        font-size: 100px;
    }
}

.abt-icon1 {
    font-size: 135px;
    position: absolute;
    left: 70%;
    top: 60%;
    color: white;
    animation: abt-icon-anim 1s ease-in-out;
}

@keyframes abt-icon-anim {
    from {
        font-size: 0px;
    }

    to {
        font-size: 135px;
    }
}

.abt-three {
    width: 100vw;
    height: 450px;
    /* border: 1px solid red; */
    margin-top: 15px;
    background-color:  #faf6f6;;
    margin-bottom: 0;
    text-align: center;
}

.abt-h2-1 {
    font-size: 54px;
}

.abt-p-1 {
    
    margin: 50px;
    padding: 81px;

    
    color: #e74c3c;
    /* font-weight: 600; */
    font-size: 19px;
}

.abt-four {
    /* border: 1px solid red; */
    height: auto;
    background-color:  #faf6f6;;
    width: 100vw;
}

.abt-four-s {
  
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


}

.abt-four-s-2 {
    background: #000000;
    background: -webkit-linear-gradient(to right, #e74c3c, #000000);
    background: linear-gradient(to right,#67bfb5, #118f81);
    /* height: 370px; */
    height: 335px;
    width: 60%;
    border-radius: 20px;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.5);
}

.abt-four-ss1 {
    /* border:1px solid white; */
    width: 30%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.abt-four-ss2 {
    /* border:1px solid white;  */
    width: 69%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.abt-img1 {
    width: 77%;
    height: 54%;
    border-radius: 100px;
    margin: 85px 0 0 22px;
}
.abt-p-1{
    color: #045c52;
}
.abt-p-2 {
    color: white;
    font-weight: 600;
    font-size: 27px;
    margin-bottom: 0;
}

.abt-p-3 {
    color: white;
    font-weight: 100;
    margin: 0;
    padding: 0;
}

.abt-p-4 {
    color: white;
    font-size: 13px;

}

.abt-four-s-3 {
    background: #000000;
    background: -webkit-linear-gradient(to right, #e74c3c, #000000);
    background: linear-gradient(to right,#7dc6bd, #0f7065);
    /* height: 370px; */
    height: 335px;
    width: 60%;
    border-radius: 20px;
    margin: auto;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.5);
    margin-top: 50px;
    margin-bottom: 70px;
}



@media (max-width:820px) {
    .abt-icon1 {
        font-size: 90px;
        left: 55%;
        animation: m-abt-icon-anim 500ms ease-in-out;
    }
.h-three-inner{
    display: flex;
    align-items: center;
    justify-content:center;
    flex-direction: column;
}
    @keyframes m-abt-icon-anim {
        from {
            font-size: 0px;
        }

        to {
            font-size: 90px;
        }
    }

    .abt-h1 {
        font-size: 45px;
        animation: m-abt-h1 1s ease-in-out;
    }

    @keyframes m-abt-h1 {
        from {
            font-size: 0px;
        }

        to {
            font-size: 45px;
        }
    }

    .abt-two {
        animation: m-abt-two 1s ease-in-out;
    }

    @keyframes m-abt-two {
        from {
            height: 0%;
        }

        to {
            height: 100%;
        }
    }

    .abt-p-1 {
        font-size: 16px;
    }

    .abt-three {
        height: auto;
    }

    .abt-four-s-2 {
        flex-direction: column;
        height: auto;
        width: 99%;
    }

    .abt-four-s-3 {
        flex-direction: column;
        height: auto;
        width: 99%;
    }

    .abt-four-ss1 {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .abt-four-ss2 {
      
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .abt-img1 {
        margin: 30px 0 0 0px;
        width: 60%;
    }

    .abt-p-4 {
        font-size: 15px;
        padding: 10px;
    }

    .abt-four-s {
        margin: auto;
    }

}